import { SelectedDate } from '../types';
import { dayEqual } from '../utils';

/** Проверить, вписывается ли дата в заданный диапазон */
export function dayDisabled(day: Date, minDate?: Date, maxDate?: Date): boolean {
  if (minDate && day < minDate) {
    return true;
  }

  if (maxDate && day > maxDate) {
    return true;
  }

  return false;
}

/** Определить дата выбрана или нет */
export function daySelected(
  day: Date,
  selectedDate?: SelectedDate
): boolean {
  if (!selectedDate) return false;

  if (dayEqual(selectedDate.from, day)) return true;

  if (selectedDate.to && dayEqual(selectedDate.to, day)) return true;

  return false;
}

/** Определить, находится ди дата в диапазоне */
export function dayInRage(day: Date, selectedDate?: SelectedDate): boolean {
  if (!selectedDate?.to) return false;

  return day >= selectedDate.from && day <= selectedDate.to;
}
